'use client'

import { useEffect, useMemo, useState } from 'react'
import { Info } from 'lucide-react'
import { HQSidebar } from '@/components/hq/HQSidebar'

/**
 * Slydes HQ — Dashboard (Picture)
 *
 * Purpose: Dashboard v1.3 (Momentum-first)
 * “Is this getting better, and what should I do next?”
 * Must stay aligned with MVP docs:
 * - Free vs Creator only (PAY-TIERS.md)
 * - Analytics locked on Free (MVP-MONETISATION.md prompt 2)
 * - Behaviour-based measurement (views, swipe depth, completion) and no vanity dashboards
 * - Enquiries do NOT live on the Dashboard — they belong on Inbox.
 */

export default function HQDashboardPage() {
  const [plan, setPlan] = useState<'free' | 'creator'>('creator')
  const [lens, setLens] = useState<'momentum' | 'stats'>('momentum')
  const [momentumStyle, setMomentumStyle] = useState<'classic' | 'v2'>('v2')

  const InfoButton = ({ label, description }: { label: string; description: string }) => {
    return (
      <button
        type="button"
        title={`${label}: ${description}`}
        aria-label={`${label}: ${description}`}
        className="inline-flex items-center justify-center w-6 h-6 rounded-md bg-transparent text-gray-400 hover:text-gray-700 transition-colors focus:outline-none focus-visible:ring-2 focus-visible:ring-blue-500/40 focus-visible:ring-offset-2 focus-visible:ring-offset-white dark:text-white/35 dark:hover:text-white/70 dark:focus-visible:ring-cyan-400/30 dark:focus-visible:ring-offset-[#1c1c1e]"
      >
        <Info className="w-4 h-4" aria-hidden="true" />
      </button>
    )
  }

  useEffect(() => {
    try {
      const stored = window.localStorage.getItem('slydes_demo_plan')
      if (stored === 'free' || stored === 'creator') setPlan(stored)
    } catch {
      // ignore
    }
  }, [])

  useEffect(() => {
    try {
      const stored = window.localStorage.getItem('slydes_demo_dashboard_lens')
      if (stored === 'momentum' || stored === 'stats') setLens(stored)
    } catch {
      // ignore
    }
  }, [])

  useEffect(() => {
    try {
      window.localStorage.setItem('slydes_demo_plan', plan)
    } catch {
      // ignore
    }
  }, [plan])

  useEffect(() => {
    try {
      window.localStorage.setItem('slydes_demo_dashboard_lens', lens)
    } catch {
      // ignore
    }
  }, [lens])

  const isCreator = plan === 'creator'

  const nextImprovement = useMemo(
    () => ({
      slydeName: 'Camping',
      frameLabel: 'Frame 3',
      leakPct: 42,
      lastChangeDays: 3,
      suggestion: 'Shorten the copy and lead with the payoff.',
      actionLabel: 'Improve this Slyde',
    }),
    []
  )

  const rankedSlydes = useMemo(
    () => [
      {
        id: 'camping' as const,
        name: 'Camping',
        completionPct: 68,
        completionDelta: 4,
        ctaClicks: 98,
        drop: 'Frame 3',
        accent: 'from-blue-600 to-cyan-500',
        frameTitle: 'Wake Up Here',
        frameSubtitle: 'Somewhere unreal',
      },
      {
        id: 'just-drive' as const,
        name: 'Just Drive',
        completionPct: 61,
        completionDelta: -2,
        ctaClicks: 58,
        drop: 'Frame 2',
        accent: 'from-blue-700 to-cyan-500',
        frameTitle: 'Just Drive',
        frameSubtitle: 'Day hire, no tent',
      },
    ],
    []
  )

  // Dashboard v1.3: momentum header + one dominant next improvement
  const totalCtaClicks = useMemo(() => rankedSlydes.reduce((acc, s) => acc + s.ctaClicks, 0), [rankedSlydes])
  const momentum = useMemo(
    () => ({
      leading: `${totalCtaClicks} CTA clicks this week — completion`,
      highlight: 'up 6%',
      trailing: 'since your last change.',
    }),
    [totalCtaClicks]
  )

  const progressAnchor = useMemo(
    () => ({
      title: 'Last improvement shipped',
      detail: 'Camping — copy shortened on Frame 3 (2 days ago)',
    }),
    []
  )

  const statsMode = useMemo(() => {
    // ═══════════════════════════════════════════════════════════════════
    // GLOBAL STATS MODE — Aggregate across ALL Slydes
    // No per-Slyde mixing. Clean definitions.
    // ═══════════════════════════════════════════════════════════════════

    const totalStarts = 1240
    const totalSlydes = rankedSlydes.length

    // TRAFFIC SOURCES — Where starts come from (aggregate)
    const startsBySource = [
      { key: 'QR', value: 520 },
      { key: 'Bio links', value: 280 },
      { key: 'Ads', value: 210 },
      { key: 'Direct', value: 160 },
      { key: 'Referral', value: 70 },
    ]
    const sourceDistribution = startsBySource.map((s) => ({
      ...s,
      pct: Math.round((s.value / totalStarts) * 100),
    }))

    // ENGAGEMENT — Global metrics
    const completionRate = 56 // % of starts that reach final frame
    const avgSwipeDepth = 72 // avg % of frames viewed per session
    const bounceRate = 12 // % left on Frame 1
    const avgTimePerFrame = 4.2 // seconds

    // DROP-OFF SHAPE — When people leave (aggregate across all Slydes)
    // Not "which Slyde" — just "which stage of the journey"
    const dropoffShape = [
      {
        stage: 'Early',
        description: 'Frame 1–2',
        pct: 46,
        // Contributors are explicitly defined as: % of ALL drop-offs that happen in this stage AND in this Slyde.
        topContributors: [
          { slydeName: 'Camping', pctOfAllDropoffs: 28 },
          { slydeName: 'Just Drive', pctOfAllDropoffs: 18 },
        ],
      },
      {
        stage: 'Mid',
        description: 'Frame 3–4',
        pct: 38,
        topContributors: [
          { slydeName: 'Camping', pctOfAllDropoffs: 22 },
          { slydeName: 'Just Drive', pctOfAllDropoffs: 16 },
        ],
      },
      {
        stage: 'Late',
        description: 'Frame 5+',
        pct: 16,
        topContributors: [
          { slydeName: 'Camping', pctOfAllDropoffs: 9 },
          { slydeName: 'Just Drive', pctOfAllDropoffs: 7 },
        ],
      },
    ]

    // CTA PERFORMANCE — Aggregate clicks
    const totalClicks = totalCtaClicks
    const clickRate = Math.round((totalClicks / totalStarts) * 1000) / 10
    const clicksBySlyde = rankedSlydes.map((s) => ({ 
      id: s.id, 
      name: s.name, 
      clicks: s.ctaClicks,
      rate: Math.round((s.ctaClicks / (totalStarts / totalSlydes)) * 1000) / 10 // approx rate per Slyde
    }))
    const bestCta = { text: 'Book now', clicks: 98, rate: 14.1, slydeName: 'Camping', frameLabel: 'Action (Frame 6)' }
    
    // Conversion by traffic source (which source converts best)
    const conversionBySource = [
      { source: 'QR', rate: 18.2 },
      { source: 'Bio links', rate: 14.8 },
      { source: 'Direct', rate: 12.4 },
      { source: 'Ads', rate: 9.1 },
    ]

    // USER FLOW — Navigation patterns (global)
    const flowPatterns = {
      topEntrySlyde: 'Camping', // Most common first Slyde viewed
      topExitSlyde: 'Just Drive', // Most common last Slyde viewed
      avgFramesViewed: 4.3, // Average frames per session
      mostSkippedPosition: 'Frame 4', // Which frame position gets skipped most (across all Slydes)
      mostRevisitedPosition: 'Frame 5', // Which frame position gets revisited most
      typicalPath: ['Profile', 'Camping', 'Availability'],
    }

    return {
      totalStarts,
      totalSlydes,
      sourceDistribution,
      completionRate,
      avgSwipeDepth,
      bounceRate,
      avgTimePerFrame,
      dropoffShape,
      dropoffContributorDefinition: '% of all drop-offs that happen in this stage AND in this Slyde',
      totalClicks,
      clickRate,
      clicksBySlyde,
      bestCta,
      conversionBySource,
      flowPatterns,
    }
  }, [rankedSlydes, totalCtaClicks])

  return (
    <div className="min-h-screen bg-gray-50 text-gray-900 dark:bg-[#1c1c1e] dark:text-white overflow-hidden">
      <div className="flex h-screen">
        {/* Sidebar */}
        <HQSidebar 
          activePage="dashboard" 
          plan={plan} 
          onPlanChange={setPlan}
        />

        {/* Main */}
        <main className="flex-1 flex flex-col overflow-hidden">
          <header className="h-16 border-b border-gray-200 flex items-center justify-between px-8 shrink-0 bg-white/80 backdrop-blur-xl dark:border-white/10 dark:bg-[#1c1c1e]/80">
            <div>
              <h1 className="text-xl font-bold font-display tracking-tight text-gray-900 dark:text-white">Dashboard</h1>
              <p className="text-sm text-gray-500 dark:text-white/60">
                {lens === 'momentum' ? 'Momentum • what matters now' : 'Stats • the full picture'}
              </p>
            </div>
            <div className="flex items-center gap-2">
              {/* Lens toggle — fixed contrast */}
              <div className="flex items-center gap-1 rounded-xl bg-gray-200 p-1 dark:bg-white/10">
                <button
                  onClick={() => setLens('momentum')}
                  className={`px-3 py-1.5 rounded-lg text-xs font-semibold transition-colors ${
                    lens === 'momentum' 
                      ? 'bg-white text-gray-900 shadow-sm dark:bg-[#3a3a3c] dark:text-white' 
                      : 'text-gray-600 hover:text-gray-900 dark:text-white/50 dark:hover:text-white'
                  }`}
                >
                  Momentum
                </button>
                <button
                  onClick={() => setLens('stats')}
                  className={`px-3 py-1.5 rounded-lg text-xs font-semibold transition-colors ${
                    lens === 'stats' 
                      ? 'bg-white text-gray-900 shadow-sm dark:bg-[#3a3a3c] dark:text-white' 
                      : 'text-gray-600 hover:text-gray-900 dark:text-white/50 dark:hover:text-white'
                  }`}
                >
                  Stats
                </button>
              </div>
              <button className="px-4 py-2 bg-gray-100 text-gray-800 font-semibold text-sm rounded-xl hover:bg-gray-200 transition-colors dark:bg-white/10 dark:text-white dark:hover:bg-white/15">
                Preview Profile
              </button>
              <button className="px-5 py-2.5 bg-gradient-to-r from-blue-600 to-cyan-500 text-white font-semibold rounded-xl hover:opacity-90 transition-opacity shadow-lg shadow-blue-500/15">
                Create Slyde
              </button>
            </div>
          </header>

          <div className="flex-1 overflow-y-auto p-8">
            {/* Dashboard v2.0: Momentum = launch pad, Stats = observatory */}
            <div className="max-w-6xl">
              {/* Lens-specific content */}
              <div className="mb-10">
                {lens === 'momentum' ? (
                  /* ═══════════════════════════════════════════════════════════
                     MOMENTUM 3.0 — One visual anchor (hero), demoted action card
                     ═══════════════════════════════════════════════════════════ */
                  <div className="space-y-8">
                    {/* Hero: THE scoreboard — one number, one context line, last win badge */}
                    <div className="relative">
                      {/* Subtle animated gradient orb */}
                      <div className="absolute -top-6 -left-6 w-32 h-32 bg-gradient-to-br from-blue-500/15 to-cyan-500/15 rounded-full blur-3xl" />
                      
                      <div className="relative">
                        <div className="flex items-baseline gap-4">
                          <span className="font-mono text-7xl md:text-8xl font-bold bg-gradient-to-r from-blue-600 to-cyan-500 bg-clip-text text-transparent tracking-tight">
                            {totalCtaClicks}
                          </span>
                          <span className="text-2xl md:text-3xl font-display font-bold text-gray-900 dark:text-white">
                            CTA clicks
                          </span>
                        </div>
                        <div className="mt-2 flex items-center gap-3 text-base text-gray-600 dark:text-white/60">
                          <span className="font-mono font-semibold text-emerald-600 dark:text-emerald-400">↑ 6%</span>
                          <span>this week</span>
                          <span className="text-gray-300 dark:text-white/20">•</span>
                          <span>{rankedSlydes.reduce((sum, s) => sum + s.completionPct, 0) / rankedSlydes.length | 0}% avg completion</span>
                        </div>
                        {/* Last win badge — inline with hero */}
                        <div className="mt-4 inline-flex items-center gap-2 px-3 py-1.5 rounded-full bg-emerald-50 border border-emerald-200/60 dark:bg-emerald-500/10 dark:border-emerald-500/20">
                          <svg className="w-3.5 h-3.5 text-emerald-600 dark:text-emerald-400" fill="none" stroke="currentColor" viewBox="0 0 24 24">
                            <path strokeLinecap="round" strokeLinejoin="round" strokeWidth={2.5} d="M5 13l4 4L19 7" />
                          </svg>
                          <span className="text-sm font-medium text-emerald-700 dark:text-emerald-300">
                            Last win: {progressAnchor.title} ({progressAnchor.detail})
                          </span>
                        </div>
                      </div>
                    </div>

                    {/* Slydes Insight — coaching voice, not a card, just a helpful sentence */}
                    <div className="flex items-start gap-3 px-1">
                      <div className="shrink-0 mt-0.5">
                        <svg className="w-4 h-4 text-blue-500 dark:text-cyan-400" fill="none" stroke="currentColor" viewBox="0 0 24 24">
                          <path strokeLinecap="round" strokeLinejoin="round" strokeWidth={2} d="M9.663 17h4.673M12 3v1m6.364 1.636l-.707.707M21 12h-1M4 12H3m3.343-5.657l-.707-.707m2.828 9.9a5 5 0 117.072 0l-.548.547A3.374 3.374 0 0014 18.469V19a2 2 0 11-4 0v-.531c0-.895-.356-1.754-.988-2.386l-.548-.547z" />
                        </svg>
                      </div>
                      <p className="text-sm text-gray-600 dark:text-white/60 leading-relaxed">
                        <span className="font-semibold text-gray-900 dark:text-white">{rankedSlydes[0]?.name}</span> is your top performer, but <span className="font-semibold text-gray-900 dark:text-white">{nextImprovement.frameLabel}</span> is where most people leave. Shortening copy here usually helps — we'd start there.
                      </p>
                    </div>

                    {/* Action Card: Demoted — secondary, not spotlight */}
                    <div className="relative rounded-2xl bg-white border border-gray-200/80 overflow-hidden dark:bg-[#2c2c2e] dark:border-white/10">
                      {/* Thin top gradient accent */}
                      <div className="absolute inset-x-0 top-0 h-1 bg-gradient-to-r from-blue-600 to-cyan-500" />
                      
                      <div className="p-6">
                        <div className="flex items-start justify-between gap-4">
                          <div className="flex-1 min-w-0">
                            <div className="flex items-center gap-2 mb-2">
                              <span className="inline-flex items-center gap-1.5 px-2 py-0.5 rounded-md bg-blue-50 text-blue-700 text-xs font-semibold uppercase tracking-wider dark:bg-blue-500/10 dark:text-blue-400">
                                <span className="w-1.5 h-1.5 rounded-full bg-blue-500 dark:bg-blue-400" />
                                Next action
                              </span>
                            </div>
                            <div className="text-lg font-display font-bold text-gray-900 dark:text-white">
                              {nextImprovement.slydeName} → {nextImprovement.frameLabel} leaking <span className="text-blue-600 dark:text-cyan-400">{nextImprovement.leakPct}%</span>
                            </div>
                            <div className="mt-1 text-sm text-gray-500 dark:text-white/50">
                              {nextImprovement.suggestion} • Last touched {nextImprovement.lastChangeDays}d ago
                            </div>
                          </div>
                          <a
                            href={`/demo/editor-mockup?slyde=camping`}
                            className="shrink-0 inline-flex items-center gap-2 px-4 py-2 rounded-xl bg-gradient-to-r from-blue-600 to-cyan-500 text-white font-semibold text-sm hover:shadow-lg hover:shadow-blue-500/20 transition-all"
                          >
                            <span>Fix</span>
                            <svg className="w-4 h-4" fill="none" stroke="currentColor" viewBox="0 0 24 24">
                              <path strokeLinecap="round" strokeLinejoin="round" strokeWidth={2} d="M13 7l5 5m0 0l-5 5m5-5H6" />
                            </svg>
                          </a>
                        </div>
                      </div>
                    </div>

                    {/* Ranked Slydes: Simplified — one metric, one action */}
                    <div>
                      <div className="flex items-center justify-between mb-4">
                        <h3 className="text-sm font-semibold uppercase tracking-wider text-gray-500 dark:text-white/50">
                          Your Slydes
                        </h3>
                        <span className="text-xs text-gray-400 dark:text-white/40">
                          Ranked by drop-off impact
                        </span>
                      </div>
                      
                      <div className="space-y-3">
                        {rankedSlydes.map((s, idx) => {
                          const isTop = idx === 0
                          return (
                            <div
                              key={s.name}
                              className={`group relative rounded-2xl p-5 transition-all hover:-translate-y-0.5 ${
                                isTop
                                  ? 'bg-gradient-to-r from-blue-50 to-cyan-50 border border-blue-200/60 dark:from-blue-500/10 dark:to-cyan-500/10 dark:border-blue-500/20'
                                  : 'bg-white border border-gray-200/60 hover:border-gray-300 dark:bg-[#2c2c2e] dark:border-white/10 dark:hover:border-white/20'
                              }`}
                            >
                              {/* Grid layout for perfect alignment */}
                              <div className="grid grid-cols-[auto_1fr_100px_60px_80px_auto] items-center gap-4">
                                {/* Rank */}
                                <div className={`w-10 h-10 rounded-xl flex items-center justify-center font-mono font-bold text-sm ${
                                  isTop
                                    ? 'bg-gradient-to-br from-blue-600 to-cyan-500 text-white'
                                    : 'bg-gray-100 text-gray-600 dark:bg-white/10 dark:text-white/70'
                                }`}>
                                  {idx + 1}
                                </div>
                                
                                {/* Name + Drop-off */}
                                <div className="min-w-0">
                                  <div className="font-display font-bold text-lg text-gray-900 dark:text-white truncate">
                                    {s.name}
                                  </div>
                                  <div className="text-sm text-gray-500 dark:text-white/50">
                                    Drop-off at <span className="font-mono font-semibold text-gray-700 dark:text-white/70">{s.drop}</span>
                                  </div>
                                </div>
                                
                                {/* Completion — fixed width column */}
                                <div className="text-right">
                                  <div className="font-mono text-xl font-bold text-gray-900 dark:text-white">
                                    {s.completionPct}%
                                  </div>
                                  <div className="text-xs text-gray-500 dark:text-white/50">completion</div>
                                </div>
                                
                                {/* Delta — fixed width column */}
                                <div className="text-right">
                                  <div className={`font-mono text-base font-semibold ${
                                    s.completionDelta > 0
                                      ? 'text-blue-700 dark:text-cyan-300'
                                      : s.completionDelta < 0
                                        ? 'text-gray-600 dark:text-white/60'
                                        : 'text-gray-400 dark:text-white/40'
                                  }`}>
                                    {s.completionDelta > 0 ? `↑+${s.completionDelta}%` : s.completionDelta < 0 ? `↓${Math.abs(s.completionDelta)}%` : '→'}
                                  </div>
                                  <div className="text-xs text-gray-500 dark:text-white/50">trend</div>
                                </div>
                                
                                {/* CTA clicks — fixed width column */}
                                <div className="text-right">
                                  <div className="font-mono text-xl font-bold text-gray-900 dark:text-white">
                                    {s.ctaClicks}
                                  </div>
                                  <div className="text-xs text-gray-500 dark:text-white/50">clicks</div>
                                </div>
                                
                                {/* Action */}
                                <a
                                  href={`/demo/editor-mockup?slyde=${encodeURIComponent(s.id)}`}
                                  className={`px-4 py-2.5 rounded-xl font-semibold text-sm transition-colors text-center ${
                                    isTop
                                      ? 'bg-gradient-to-r from-blue-600 to-cyan-500 text-white hover:opacity-90'
                                      : 'bg-gray-900 text-white hover:bg-gray-800 dark:bg-white dark:text-gray-900 dark:hover:bg-white/90'
                                  }`}
                                >
                                  {isTop ? 'Fix' : 'Edit'}
                                </a>
                              </div>
                            </div>
                          )
                        })}
                      </div>
                    </div>
                  </div>
                ) : (
                  /* ═══════════════════════════════════════════════════════════════════
                     STATS MODE — Global aggregate view across ALL Slydes
                     Clean definitions. No per-Slyde mixing.
                     ═══════════════════════════════════════════════════════════════════ */
                  <div className="space-y-6">
                    {/* Stats header */}
                    <div className="flex items-center justify-between">
                      <div className="flex items-center gap-3">
                        <div className="w-2 h-2 rounded-full bg-gradient-to-r from-blue-600 to-cyan-500" />
                        <span className="text-xs font-semibold uppercase tracking-wider text-gray-500 dark:text-white/50">
                          Aggregate performance • Last 7 days • {statsMode.totalSlydes} Slydes
                        </span>
                      </div>
                    </div>

                    {/* Top metrics row — Global totals */}
                    <div className="grid grid-cols-2 md:grid-cols-4 gap-4">
                      {[
                        {
                          label: 'Total starts',
                          value: statsMode.totalStarts.toLocaleString(),
                          sub: 'across all Slydes',
                          info: 'Sessions that begin on any Slyde (profile → first Slyde view).',
                        },
                        {
                          label: 'Completion rate',
                          value: `${statsMode.completionRate}%`,
                          sub: `${statsMode.avgSwipeDepth}% avg depth`,
                          info: 'Percent of starts that reach the final frame of the Slyde they entered.',
                        },
                        {
                          label: 'CTA clicks',
                          value: statsMode.totalClicks.toString(),
                          sub: `${statsMode.clickRate}% click rate`,
                          info: 'Total CTA button taps across all frames and all Slydes.',
                        },
                        {
                          label: 'Bounce rate',
                          value: `${statsMode.bounceRate}%`,
                          sub: `${statsMode.avgTimePerFrame}s per frame`,
                          info: 'Percent of starts that exit on Frame 1 (no meaningful progression).',
                        },
                      ].map((m) => (
                        <div
                          key={m.label}
                          className="p-4 rounded-xl bg-gradient-to-br from-gray-50 to-white border border-gray-200/60 dark:from-[#2c2c2e] dark:to-[#3a3a3c] dark:border-white/10"
                        >
                          <div className="flex items-center justify-between gap-2">
                            <div className="text-xs font-semibold uppercase tracking-wider text-gray-500 dark:text-white/50">{m.label}</div>
                            <InfoButton label={m.label} description={m.info} />
                          </div>
                          <div className="mt-1 font-mono text-2xl font-bold text-gray-900 dark:text-white">{m.value}</div>
                          <div className="text-xs text-gray-500 dark:text-white/40">{m.sub}</div>
                        </div>
                      ))}
                    </div>

                    {/* Main 2x2 grid */}
                    <div className="grid grid-cols-1 md:grid-cols-2 gap-6">
                      {/* 1. Traffic Sources — Aggregate starts by source */}
                      <div className="relative rounded-2xl bg-white border border-gray-200/60 p-6 dark:bg-[#2c2c2e] dark:border-white/10">
                        <div className="flex items-center gap-2 mb-4">
                          <div className="w-8 h-8 rounded-lg bg-gradient-to-br from-blue-600 to-cyan-500 flex items-center justify-center">
                            <svg className="w-4 h-4 text-white" fill="none" stroke="currentColor" viewBox="0 0 24 24">
                              <path strokeLinecap="round" strokeLinejoin="round" strokeWidth={2} d="M13 7h8m0 0v8m0-8l-8 8-4-4-6 6" />
                            </svg>
                          </div>
                          <div>
                            <div className="text-sm font-semibold text-gray-900 dark:text-white">Traffic Sources</div>
                            <div className="text-xs text-gray-500 dark:text-white/50">Where all {statsMode.totalStarts.toLocaleString()} starts come from</div>
                          </div>
                        </div>
                        <div className="space-y-3">
                          {statsMode.sourceDistribution.map((s) => (
                            <div key={s.key} className="flex items-center gap-3">
                              <div className="flex-1 min-w-0">
                                <div className="flex items-center justify-between mb-1">
                                  <span className="text-sm text-gray-700 dark:text-white/80">{s.key}</span>
                                  <span className="font-mono text-sm font-semibold text-gray-900 dark:text-white">{s.value.toLocaleString()}</span>
                                </div>
                                <div className="h-1.5 bg-gray-100 rounded-full overflow-hidden dark:bg-white/10">
                                  <div 
                                    className="h-full bg-gradient-to-r from-blue-600 to-cyan-500 rounded-full"
                                    style={{ width: `${s.pct}%` }}
                                  />
                                </div>
                              </div>
                              <span className="font-mono text-xs text-gray-500 dark:text-white/50 w-10 text-right">{s.pct}%</span>
                            </div>
                          ))}
                        </div>
                        <div className="absolute bottom-4 right-4">
                          <InfoButton
                            label="Traffic Sources"
                            description="Starts by source (global). Percentages are share of total starts across all Slydes."
                          />
                        </div>
                      </div>

                      {/* 2. Drop-off Shape — When people leave (aggregate, NOT per-Slyde) */}
                      <div className="relative rounded-2xl bg-white border border-gray-200/60 p-6 dark:bg-[#2c2c2e] dark:border-white/10">
                        <div className="flex items-center gap-2 mb-4">
                          <div className="w-8 h-8 rounded-lg bg-gradient-to-br from-blue-600 to-cyan-500 flex items-center justify-center">
                            <svg className="w-4 h-4 text-white" fill="none" stroke="currentColor" viewBox="0 0 24 24">
                              <path strokeLinecap="round" strokeLinejoin="round" strokeWidth={2} d="M9 19v-6a2 2 0 00-2-2H5a2 2 0 00-2 2v6a2 2 0 002 2h2a2 2 0 002-2zm0 0V9a2 2 0 012-2h2a2 2 0 012 2v10m-6 0a2 2 0 002 2h2a2 2 0 002-2m0 0V5a2 2 0 012-2h2a2 2 0 012 2v14a2 2 0 01-2 2h-2a2 2 0 01-2-2z" />
                            </svg>
                          </div>
                          <div>
                            <div className="text-sm font-semibold text-gray-900 dark:text-white">Drop-off Shape</div>
                            <div className="text-xs text-gray-500 dark:text-white/50">When people leave (all Slydes combined)</div>
                          </div>
                        </div>
                        <div className="space-y-3">
                          {statsMode.dropoffShape.map((d) => (
                            <div key={d.stage} className="space-y-2">
                              <div className="flex items-center gap-3">
                                <div className="flex-1 min-w-0">
                                  <div className="flex items-center justify-between mb-1">
                                    <div>
                                      <span className="text-sm font-semibold text-gray-900 dark:text-white">{d.stage}</span>
                                      <span className="text-xs text-gray-500 dark:text-white/50 ml-2">({d.description})</span>
                                    </div>
                                    <span
                                      className={`font-mono text-sm font-bold ${
                                        d.pct > 40
                                          ? 'text-blue-700 dark:text-cyan-300'
                                          : d.pct > 25
                                            ? 'text-blue-600 dark:text-cyan-400'
                                            : 'text-gray-700 dark:text-white/70'
                                      }`}
                                    >
                                      {d.pct}%
                                    </span>
                                  </div>
                                  <div className="h-1.5 bg-gray-100 rounded-full overflow-hidden dark:bg-white/10">
                                    <div
                                      className="h-full rounded-full bg-gradient-to-r from-blue-600 to-cyan-500"
                                      style={{ width: `${d.pct}%` }}
                                    />
                                  </div>
                                </div>
                              </div>

                              {/* Top contributors (explicitly labelled) */}
                              {'topContributors' in d && d.topContributors && d.topContributors.length > 0 && (
                                <div className="flex flex-wrap items-center gap-2">
                                  <span className="text-xs text-gray-500 dark:text-white/50">Top contributors:</span>
                                  {d.topContributors.map((c: any) => (
                                    <span
                                      key={`${d.stage}-${c.slydeName}`}
                                      className="inline-flex items-center gap-1 px-2 py-1 rounded-lg bg-gray-100 text-gray-700 text-xs font-medium dark:bg-white/10 dark:text-white/70"
                                    >
                                      <span>{c.slydeName}</span>
                                      <span className="font-mono font-semibold">{c.pctOfAllDropoffs}%</span>
                                    </span>
                                  ))}
                                </div>
                              )}
                            </div>
                          ))}
                        </div>
                        <div className="mt-4 pt-4 border-t border-gray-100 dark:border-white/10 text-xs text-gray-500 dark:text-white/50">
                          % of all drop-offs that occur at each stage
                        </div>
                        <div className="absolute bottom-4 right-4">
                          <InfoButton
                            label="Drop-off Shape"
                            description={`Stage bars are global. \"Top contributors\" are ${statsMode.dropoffContributorDefinition}.`}
                          />
                        </div>
                      </div>

                      {/* 3. CTA Performance — Clicks by Slyde + best CTA */}
                      <div className="relative rounded-2xl bg-white border border-gray-200/60 p-6 dark:bg-[#2c2c2e] dark:border-white/10">
                        <div className="flex items-center gap-2 mb-4">
                          <div className="w-8 h-8 rounded-lg bg-gradient-to-br from-blue-600 to-cyan-500 flex items-center justify-center">
                            <svg className="w-4 h-4 text-white" fill="none" stroke="currentColor" viewBox="0 0 24 24">
                              <path
                                strokeLinecap="round"
                                strokeLinejoin="round"
                                strokeWidth={2}
                                d="M15 15l-2 5L9 9l11 4-5 2zm0 0l5 5M7.188 2.239l.777 2.897M5.136 7.965l-2.898-.777M13.95 4.05l-2.122 2.122m-5.657 5.656l-2.12 2.122"
                              />
                            </svg>
                          </div>
                          <div>
                            <div className="text-sm font-semibold text-gray-900 dark:text-white">CTA Performance</div>
                            <div className="text-xs text-gray-500 dark:text-white/50">Clicks by Slyde</div>
                          </div>
                        </div>

                        {/* Clicks by Slyde */}
                        <div className="space-y-2 mb-4">
                          {statsMode.clicksBySlyde.map((s) => (
                            <div key={s.id} className="flex items-center justify-between p-3 rounded-xl bg-gray-50 dark:bg-white/5">
                              <span className="text-sm font-semibold text-gray-900 dark:text-white">{s.name}</span>
                              <div className="flex items-center gap-3">
                                <span className="font-mono text-sm text-gray-900 dark:text-white">{s.clicks} clicks</span>
                                <span className="font-mono text-xs px-2 py-0.5 rounded-full bg-gradient-to-r from-blue-100 to-cyan-100 text-blue-700 dark:from-blue-500/20 dark:to-cyan-500/20 dark:text-cyan-300">
                                  {s.rate}%
                                </span>
                              </div>
                            </div>
                          ))}
                        </div>

                        {/* Best CTA (explicitly attributed) */}
                        <div className="p-3 rounded-xl bg-gradient-to-r from-blue-50 to-cyan-50 border border-blue-100 dark:from-blue-500/10 dark:to-cyan-500/10 dark:border-blue-500/20">
                          <div className="text-xs text-gray-500 dark:text-white/50 uppercase tracking-wider mb-1">Best performing CTA</div>
                          <div className="flex items-start justify-between gap-3">
                            <div className="min-w-0">
                              <div className="text-sm font-semibold text-gray-900 dark:text-white truncate">
                                "{statsMode.bestCta.text}"
                              </div>
                              <div className="text-xs text-gray-600 dark:text-white/60">
                                {statsMode.bestCta.slydeName} <span className="text-gray-400 dark:text-white/30 mx-1">•</span> {statsMode.bestCta.frameLabel}
                              </div>
                            </div>
                            <div className="shrink-0 font-mono text-sm text-blue-700 dark:text-cyan-300">
                              {statsMode.bestCta.clicks} clicks • {statsMode.bestCta.rate}%
                            </div>
                          </div>
                        </div>

                        {/* Conversion by source */}
                        <div className="mt-4 pt-4 border-t border-gray-100 dark:border-white/10">
                          <div className="text-xs text-gray-500 dark:text-white/50 uppercase tracking-wider mb-2">Click rate by traffic source</div>
                          <div className="grid grid-cols-2 gap-2">
                            {statsMode.conversionBySource.map((c) => (
                              <div key={c.source} className="flex items-center justify-between text-sm">
                                <span className="text-gray-600 dark:text-white/60">{c.source}</span>
                                <span className="font-mono font-semibold text-gray-900 dark:text-white">{c.rate}%</span>
                              </div>
                            ))}
                          </div>
                        </div>

                        <div className="absolute bottom-4 right-4">
                          <InfoButton
                            label="CTA Performance"
                            description="Clicks are total CTA taps. Best CTA includes the Slyde + frame where that CTA lives."
                          />
                        </div>
                      </div>

                      {/* 4. User Flow — Navigation patterns (global) */}
                      <div className="relative rounded-2xl bg-white border border-gray-200/60 p-6 dark:bg-[#2c2c2e] dark:border-white/10">
                        <div className="flex items-center gap-2 mb-4">
                          <div className="w-8 h-8 rounded-lg bg-gradient-to-br from-blue-600 to-cyan-500 flex items-center justify-center">
                            <svg className="w-4 h-4 text-white" fill="none" stroke="currentColor" viewBox="0 0 24 24">
                              <path strokeLinecap="round" strokeLinejoin="round" strokeWidth={2} d="M9 20l-5.447-2.724A1 1 0 013 16.382V5.618a1 1 0 011.447-.894L9 7m0 13l6-3m-6 3V7m6 10l4.553 2.276A1 1 0 0021 18.382V7.618a1 1 0 00-.553-.894L15 4m0 13V4m0 0L9 7" />
                            </svg>
                          </div>
                          <div>
                            <div className="text-sm font-semibold text-gray-900 dark:text-white">User Flow</div>
                            <div className="text-xs text-gray-500 dark:text-white/50">Navigation patterns across all sessions</div>
                          </div>
                        </div>
                        <div className="grid grid-cols-2 gap-3">
                          <div className="p-3 rounded-xl bg-gray-50 dark:bg-white/5">
                            <div className="text-xs text-gray-500 dark:text-white/50 mb-1">Top entry Slyde</div>
                            <div className="text-sm font-semibold text-gray-900 dark:text-white">{statsMode.flowPatterns.topEntrySlyde}</div>
                          </div>
                          <div className="p-3 rounded-xl bg-gray-50 dark:bg-white/5">
                            <div className="text-xs text-gray-500 dark:text-white/50 mb-1">Top exit Slyde</div>
                            <div className="text-sm font-semibold text-gray-900 dark:text-white">{statsMode.flowPatterns.topExitSlyde}</div>
                          </div>
                          <div className="p-3 rounded-xl bg-gray-50 dark:bg-white/5">
                            <div className="text-xs text-gray-500 dark:text-white/50 mb-1">Most skipped position</div>
                            <div className="text-sm font-semibold text-gray-900 dark:text-white">{statsMode.flowPatterns.mostSkippedPosition}</div>
                          </div>
                          <div className="p-3 rounded-xl bg-gray-50 dark:bg-white/5">
                            <div className="text-xs text-gray-500 dark:text-white/50 mb-1">Most revisited position</div>
                            <div className="text-sm font-semibold text-gray-900 dark:text-white">{statsMode.flowPatterns.mostRevisitedPosition}</div>
                          </div>
                        </div>
                        <div className="mt-4 p-3 rounded-xl bg-gray-50 dark:bg-white/5">
                          <div className="text-xs text-gray-500 dark:text-white/50 mb-1">Avg frames per session</div>
                          <div className="font-mono text-lg font-bold text-gray-900 dark:text-white">{statsMode.flowPatterns.avgFramesViewed}</div>
                        </div>
                        <div className="mt-4 pt-4 border-t border-gray-100 dark:border-white/10">
                          <div className="text-xs text-gray-500 dark:text-white/50 uppercase tracking-wider mb-2">Typical journey</div>
                          <div className="flex items-center gap-2 text-sm flex-wrap">
                            {statsMode.flowPatterns.typicalPath.map((step, i, arr) => (
                              <span key={step} className="flex items-center gap-2">
                                <span className="px-2 py-1 rounded-lg bg-gray-100 text-gray-700 font-medium dark:bg-white/10 dark:text-white">{step}</span>
                                {i < arr.length - 1 && <span className="text-gray-400 dark:text-white/30">→</span>}
                              </span>
                            ))}
                          </div>
                        </div>

                        <div className="absolute bottom-4 right-4">
                          <InfoButton
                            label="User Flow"
                            description="High-level navigation patterns (aggregate). No individual journeys."
                          />
                        </div>
                      </div>
                    </div>
                  </div>
                )}
              </div>

              {/* Old momentum content removed — now integrated above */}
            </div>
          </div>
        </main>
      </div>

      {/* Ambient glow */}
      <div className="fixed top-0 left-1/4 w-[600px] h-[600px] bg-blue-500/10 rounded-full blur-[180px] pointer-events-none dark:bg-blue-500/12" />
      <div className="fixed bottom-0 right-1/4 w-[520px] h-[520px] bg-cyan-500/10 rounded-full blur-[180px] pointer-events-none dark:bg-cyan-500/12" />
    </div>
  )
}


